"use client"

import { useEffect, useRef, useState } from 'react'
import { Application } from '@splinetool/runtime'

export function SplineModel() {
  const canvasRef = useRef<HTMLCanvasElement>(null)
  const [isLoaded, setIsLoaded] = useState(false)
  const [error, setError] = useState<string | null>(null)

  useEffect(() => {
    if (!canvasRef.current) return

    const app = new Application(canvasRef.current)
    
    app.load('https://prod.spline.design/6Wq1Q7YGyY-iab9i/scene.splinecode')
      .then(() => {
        setIsLoaded(true)
        console.log('Spline scene loaded successfully')
      })
      .catch((err) => {
        console.error('Error loading Spline scene:', err)
        setError('Failed to load 3D model')
      })

    return () => {
      app.dispose()
    }
  }, [])

  return (
    <div className="absolute inset-0 z-0">
      {!isLoaded && !error && (
        <div className="absolute inset-0 flex items-center justify-center">
          <div className="w-16 h-16 border-4 border-white/20 border-t-white/80 rounded-full animate-spin" />
        </div>
      )}
      {error && (
        <div className="absolute inset-0 flex items-center justify-center text-white/80">
          {error}
        </div>
      )}
      <canvas
        ref={canvasRef}
        className={`w-full h-full ${isLoaded ? 'opacity-100' : 'opacity-0'} transition-opacity duration-1000`}
      />
    </div>
  )
}
